import { css, modifier, styled, variants } from '@lokalise/styled';

import type { TagProps } from './Tag';
import { oneLineEllipsis } from '../../utils/styles';
import { spacing, typography, value } from '../../utils/theme';

interface StyledTagProps {
	$variant: NonNullable<TagProps['variant']>;
	$color: string;
	$size: NonNullable<TagProps['size']>;
	$fullWidth?: boolean;
}

const baseStyle = css<StyledTagProps>`
	${oneLineEllipsis};
	box-sizing: border-box;
	${typography('body.tiny.strong')};
	border-radius: ${value('radius.tag')};
	border-width: 1px;
	border-style: solid;
	border-color: ${(props) => props.$color};
	max-width: max-content;
	width: min-content;
	line-height: 14px; // This is needed so the text appears centered
	height: 20px;
	padding: 2px ${spacing(2)};

	${variants('$variant', {
		solid: css<StyledTagProps>`
			color: ${value('color.text.inverse')};
			background-color: ${(props) => props.$color};
		`,
		outlined: css<StyledTagProps>`
			color: ${(props) => props.$color};
			background-color: transparent;
		`,
	})};

	${variants('$size', {
		default: css<StyledTagProps>`
			line-height: 14px; // This is needed so the text appears centered
			height: 20px;
			padding: 2px ${spacing(2)};
		`,
		small: css<StyledTagProps>`
			font-size: 10px;
			line-height: 1;
			padding: 1px 5px 2px;
			height: auto;
		`,
	})};

	${modifier(
		'$fullWidth',
		css`
			width: 100%;
			display: inline-block;
		`,
	)}
`;

export const SimpleTag = styled.div<StyledTagProps>`
	${baseStyle};
`;

export const TagButton = styled.button<StyledTagProps>`
	${baseStyle};

	cursor: pointer;

	&:hover {
		filter: brightness(1.1);
	}
`;
